<template>
  <div>
    <el-row>
      <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
        <h1 class="pageTitle">{{ this.pageTitle }}</h1>
      </el-col>

      <el-col id="listData" :xs="24" :sm="24" :md="24" :lg="24" :xl="24" style="background: #fff;">
        <el-row>
          <el-col :span="24">
            <el-form ref="form" :model="form" label-width="160px">
              <el-form-item label="Primeiro Nome">
                <el-input v-model="form.first_name"></el-input>
              </el-form-item>

              <el-form-item label="Sobrenome">
                <el-input v-model="form.last_name"></el-input>
              </el-form-item>

              <el-form-item label="Cargo">
                <el-input v-model="form.job_title"></el-input>
              </el-form-item>

              <el-form-item label="E-mail">
                <el-input v-model="form.email"></el-input>
              </el-form-item>

              <el-form-item label="Telefone">
                <el-input v-model="form.phone"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="updateData()">Salvar</el-button>
                <el-button>Cancelar</el-button>
              </el-form-item>
            </el-form>
          </el-col>
        </el-row>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: "UserProfile",
  data() {
    return {
      pageTitle: "Perfil do Usuário",
      form: {
        first_name: "",
        last_name: "",
        email: "",
        job_title: "",
        phone: "",
      },
    };
  },
  head: {
    title: {
      inner: "Perfil do Usuário",
      complement: "GuiaSales",
    },
  },
  methods: {
    successUpload() {
      console.log(this.data);
    },
    updateData() {
      this.$http.put("users", this.form).then((res) => {
        res;
        this.getData();
      });
    },
    getData() {
      this.$http.get("users").then((res) => {
        res;

        this.form.first_name = res.data.user_firstname;
        this.form.last_name = res.data.user_lastname;
        this.form.email = res.data.user_email;
        this.form.job_title = res.data.user_job_title;
        this.form.phone = res.data.user_phone;
      });
    },
  },
  mounted() {
    this.getData();
  },
};
</script>

